import React from 'react'
import Nav from './Nav'
import Footer from './Footer'
import { Outlet } from 'react-router';
import Navbar from './Navbar';
function Container() {
  return (
    <>
    <div>
    <Navbar/>
    <Nav/>
   <Outlet/>
    <Footer />
    </div>
    </>
  )
}

export default Container
