import React from 'react'
import './Styles/nav.css'
const categories =[
    {id:'1',title:"ALL"},
    {id:'2',title:"Amazon minTV"},
    {id:'3',title:"Sell"},
    {id:'4',title:"Best seller"},
    {id:'5',title:"Today's Deals"},
    {id:'6',title:"Mobiles"},
    {id:'7',title:"New Releases"},
    {id:'8',title:"Customer Service"},
    {id:'9',title:"Prime"},
    {id:'10',title:"Electronics"},
    {id:'11',title:"Gift ideas"},
    {id:'12',title:"Home & Kitchen"},
    {id:'13',title:"fashion"},
    {id:'14',title:"Computers"},
    {id:'15',title:"Amazon pay"}
]
function Nav() {
  return (
    <>
<div className="nav1" id="nav1">
    {categories.map((item,index)=>{
        return <div key={index} className='nav1t'>{item.title}</div>
    })}
</div>
    </>
  )
}

export default Nav
